.HexagonContainer {
    display: flex;
    flex-direction: row;
    margin: 1px;
}

:root {
    --hexColor: #d3d5c0;
    --textBorderColor : black;
}

.LeftPart {
    width : 0;
    /* border-right: 30px solid var(--hexColor); */
    border-top: 52px solid transparent;
    border-bottom: 52px solid transparent;
}

.RightPart {
    width : 0;
    /* border-left: 30px solid var(--hexColor); */
    border-top: 52px solid transparent;
    border-bottom: 52px solid transparent;
}

.CenterPart {
    width: 68px;
    height: 104px;
    /* background-color: var(--hexColor); */
}

.TextContainer {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
}

.QueueNumberContainer {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-evenly;
    text-shadow: 2px 0 2px var(--textBorderColor), -2px 0 2px var(--textBorderColor), 0 2px 2px var(--textBorderColor), 0 -2px 2px var(--textBorderColor),
    1px 1px 2px var(--textBorderColor), -1px -1px 2px var(--textBorderColor), 1px -1px 2px var(--textBorderColor), -1px 1px 2px var(--textBorderColor);
}

.WardenColor {
    color: #467aa5;
    font-weight: bold;
    /* font-size: 20px; */
}

.ColonialColor {
    color: #64865d;
    font-weight: bold;
    /* font-size: 20px; */
}

.MapNameContainer {
    font-weight: 600;
}