
.CounterContainer {
    display: flex;
    flex-direction: row;
    width: 100%;
    font-size: 18px;
    justify-content: space-around;
    align-items: center;
    color: black;
}

.WardenCounter {
    background-color: #467aa5;
    border-radius: 20px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ColonialCounter {
    background-color: #64865d;
    border-radius: 20px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ColonialCountNumber {
    font-size: 32px;
    text-shadow: 2px 0 2px var(--textBorderColor), -2px 0 2px var(--textBorderColor), 0 2px 2px var(--textBorderColor), 0 -2px 2px var(--textBorderColor),
    1px 1px 2px var(--textBorderColor), -1px -1px 2px var(--textBorderColor), 1px -1px 2px var(--textBorderColor), -1px 1px 2px var(--textBorderColor);
    color: #64865d;
}

.WardenCountNumber {
    font-size: 32px;
    text-shadow: 2px 0 2px var(--textBorderColor), -2px 0 2px var(--textBorderColor), 0 2px 2px var(--textBorderColor), 0 -2px 2px var(--textBorderColor),
    1px 1px 2px var(--textBorderColor), -1px -1px 2px var(--textBorderColor), 1px -1px 2px var(--textBorderColor), -1px 1px 2px var(--textBorderColor);
    color: #467aa5;
}